<template>
    <div class="step-result">
        <div class="text-center mb-3">
            <button class="btn-custom mr-3" @click="saveResult">저 장 하 기</button>
            <button class="btn-custom mr-3" @click="sendEmail">이 메 일 전 송</button>
            <button class="btn-custom" @click="$router.push('/')">다 시 하 기</button>
        </div>
        <div class="mb-12">
            <div ref="result" :class="`m-auto outter-frame outter-frame-${columns}-${rows}`">
                <result-frame :rows="rows" :columns="columns"></result-frame>
            </div>
        </div>
    </div>
</template>

<script>
import resultFrame from './frames/resultFrame.vue';
import html2canvas from 'html2canvas';

export default {
    name: 'step-result',
    components: {
        resultFrame,
    },
    data() {
        return {
            rows: 2,
            columns: 1,
        }
    },
    created() {
        let table = this.$store.getters.getTable;

        this.rows = table.rows;
        this.columns = table.columns;
    },
    methods: {
        async saveResult() {
            let result = await html2canvas(this.$refs.result);
            result.style.backgroundImage = this.$store.getters.getFrameImg;

            let a = document.createElement("a");
            let url = result.toDataURL();

            a.href = url;
            a.style.width = 620+"px";
            a.style.height = 1755+"px";
            a.download = "Image.png";
            a.click();
        },
        async sendEmail() {
            this.$swal({
                title: '이메일 주소를 입력해주세요',
                input: 'text',
                inputAttributes: {
                    autocapitalize: 'off'
                },
                showCancelButton: true,
                confirmButtonText: '전송하기',
                cancelButtonText: '취소',
            })
            .then((result) => {
                if (result.value) {
                    this.$swal.fire({
                        title: '완료!',
                        text: `'${result.value}' 이메일이 성공적으로 전송되었습니다!`,
                    })

                    emailPostToBackend(result.value);
                }
            })

            const emailPostToBackend = async (email) => {
                const result = await html2canvas(this.$refs.result);
                result.style.backgroundImage = this.$store.getters.getFrameImg;
                const url = result.toDataURL();

                fetch("http://127.0.0.1:5000/api/photo-send-email", {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json",
                    },
                    body: JSON.stringify({
                        url: url,
                        email: email
                    })
                })
                    .then((response) => response.json())
                    .then((result) => {
                        if(!result.result) {
                            alert("문제가 발생했습니다.");
                        }
                    })
                    .catch((error) => {
                        console.log("error:", error);
                        alert("문제가 발생했습니다.");
                    });
                }
        },
    },
}
</script>

